import React from "react";
import { Link } from "react-router-dom";

export default class NavigationComponent extends React.Component {
  render() { 
    const isSelected = (target) => target === this.props.current;
    const getSelectedClass = (target) => isSelected(target) ? "selected" : ""; 
    const getLink = (page) => 
      <Link to={page.path} className={getSelectedClass(page.name)}>
        {page.display}
      </Link>
    const links = pages.map(getLink);

    return <div className="navigation-header" style={{color: this.props.color}}>
            {links}
        </div>;
  }
}

export const pages = [
  {
    display: "Home",
    name: "home",
    path: "/"
  },
  {
    display: "Education",
    name: "education",
    path: "/education"
  },
  {
    display: "Projects",
    name: "projects",
    path: "/projects"
  }
]